<script>
  import { createEventDispatcher } from "svelte";

  const dispatch = createEventDispatcher();
  let result = {
    horseId: null,
    date: "",
    cityShow: "",
    country: "",
    discipline: "",
    compNr: "",
    rank: "",
    result: "",
    price: "",
  };

  const addResult = () => {
    dispatch("addResult", { horseId: result.horseId, result });
    // Reset form
    result = {
      horseId: null,
      date: "",
      cityShow: "",
      country: "",
      discipline: "",
      compNr: "",
      rank: "",
      result: "",
      price: "",
    };
  };
</script>

<section class="section">
  <div class="container">
    <h1 class="title">Add Competition Result</h1>
    <div class="card">
      <div class="card-content">
        <form on:submit|preventDefault={addResult}>
          <!-- Horse ID -->
          <div class="field">
            <label class="label" for="horseId">Horse ID<span class="has-text-danger">*</span></label>
            <div class="control">
              <input
                class="input"
                type="number"
                id="horseId"
                bind:value={result.horseId}
                required
                placeholder="Enter valid Horse ID"
              />
            </div>
            <p class="help">This must match an existing horse's ID.</p>
          </div>

          <!-- Date -->
          <div class="field">
            <label class="label" for="date">Date<span class="has-text-danger">*</span></label>
            <div class="control">
              <input class="input" type="date" id="date" bind:value={result.date} required />
            </div>
            <p class="help">The date of the competition (e.g., 2023-06-15).</p>
          </div>

          <!-- City/Show -->
          <div class="field">
            <label class="label" for="cityShow">City/Show<span class="has-text-danger">*</span></label>
            <div class="control">
              <input
                class="input"
                type="text"
                id="cityShow"
                bind:value={result.cityShow}
                required
                placeholder="e.g. Aachen CHIO"
              />
            </div>
          </div>

          <!-- Country -->
          <div class="field">
            <label class="label" for="country">Country<span class="has-text-danger">*</span></label>
            <div class="control">
              <input 
                class="input" 
                type="text" 
                id="country" 
                bind:value={result.country} 
                required 
                placeholder="e.g. Germany"
              />
            </div>
          </div>

          <!-- Discipline -->
          <div class="field">
            <label class="label" for="discipline">Discipline<span class="has-text-danger">*</span></label>
            <div class="control">
              <input
                class="input"
                type="text"
                id="discipline"
                bind:value={result.discipline}
                required
                placeholder="e.g. Show Jumping"
              />
            </div>
            <p class="help">The competition discipline (e.g. Show Jumping, Dressage).</p>
          </div>

          <!-- Competition Number -->
          <div class="field">
            <label class="label" for="compNr">Comp. Nr.<span class="has-text-danger">*</span></label>
            <div class="control">
              <input 
                class="input" 
                type="text" 
                id="compNr" 
                bind:value={result.compNr} 
                required 
                placeholder="e.g. #25"
              />
            </div>
          </div>

          <!-- Rank -->
          <div class="field">
            <label class="label" for="rank">Rank<span class="has-text-danger">*</span></label>
            <div class="control">
              <input 
                class="input" 
                type="number" 
                id="rank" 
                bind:value={result.rank} 
                required 
                placeholder="e.g. 1 for first place"
              />
            </div>
          </div>

          <!-- Result -->
          <div class="field">
            <label class="label" for="result">Result<span class="has-text-danger">*</span></label>
            <div class="control">
              <input
                class="input"
                type="number"
                step="0.01"
                id="result"
                bind:value={result.result}
                required
                placeholder="e.g. 75.50"
              />
            </div>
            <p class="help">The horse's score or time, depending on discipline.</p>
          </div>

          <!-- Price -->
          <div class="field">
            <label class="label" for="price">Price (if any)<span class="has-text-danger">*</span></label>
            <div class="control">
              <input
                class="input"
                type="number"
                step="0.01"
                id="price"
                bind:value={result.price}
                required
                placeholder="e.g. 2500.00"
              />
            </div>
            <p class="help">Monetary prize won (in local currency), if applicable.</p>
          </div>

          <!-- Submit Button -->
          <div class="field mt-5">
            <div class="control">
              <button type="submit" class="button is-primary is-fullwidth">Add Result</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</section>

<style>
  .title {
    margin-bottom: 1rem;
  }
  .card {
    margin-top: 1rem;
  }
</style>
