<script>
  import { onMount } from 'svelte';
  import { Router, Link, Route } from 'svelte-routing';
  import {
    isAuthenticated,
    user,
    initAuth,
    login,
    logout,
    getToken,
    isAuth0ClientReady,
  } from './auth.js';

  // Components
  import ViewHorses from './ViewHorses.svelte';
  import MyHorses from './MyHorses.svelte';
  import Horse from './Horse.svelte';
  import AddHorse from './AddHorse.svelte';
  import AddCompetitionResult from './AddCompetitionResult.svelte';
  import MatchHorses from './MatchHorses.svelte';
  import AutoMatch from './AutoMatch.svelte';
  import OneVsMany from './OneVsMany.svelte';
  import Chat from './Chat.svelte';

  let horses = [];
  let page = 1;
  let per_page = 100;
  let isLoading = false;
  let appReady = false;
  let error = null;

  const fetchHorses = async (page, per_page, params = {}) => {
    if (!$isAuthenticated) {
      console.log('User not authenticated');
      return;
    }

    try {
      isLoading = true;
      error = null;
      const token = await getToken();
      console.log('Token:', token?.substring(0, 10) + '...');

      if (!token) {
        throw new Error('No authentication token available');
      }

      const paramsString = Object.entries(params)
        .reduce((prev, [key, value]) => `${prev}&${key}=${value}`, '');

      const response = await fetch(
        `http://127.0.0.1:5000/horses?page=${page}&per_page=${per_page}${paramsString}`,
        {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          credentials: 'include'
        }
      );
      
      if (!response.ok) {
        console.error('Response status:', response.status);
        console.error('Response status text:', response.statusText);
        const errorText = await response.text();
        console.error('Error response:', errorText);
        throw new Error('Failed to fetch horses');
      }

      const horseData = await response.json();
      horses = horseData.data;
    } catch (error) {
      console.error('Error fetching horses:', error);
      error = 'Failed to fetch horses. Please try again.';
      horses = [];
    } finally {
      isLoading = false;
    }
  };

  // Watch for auth state changes to fetch horses
  $: if ($isAuthenticated && appReady) {
    fetchHorses(page, per_page);
  }

  const addHorse = async (event) => {
    try {
      const token = await getToken();
      if (!token) {
        throw new Error('No authentication token available');
      }

      // Add the horse using the API
      const response = await fetch(
        'http://127.0.0.1:5000/my_horses',
        {
          method: 'POST',
          body: JSON.stringify({ horse_id: event.detail.id }),
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          credentials: 'include'
        }
      );

      if (!response.ok) {
        throw new Error('Failed to add horse');
      }

      horses = [...horses, event.detail];
      console.log('Horse added successfully:', event.detail);
    } catch (error) {
      console.error('Error adding horse:', error);
    }
  };

  const addCompetitionResult = (event) => {
    let horse = horses.find((h) => h.id === event.detail.horseId);
    if (horse) {
      horse.competitionResults = [
        ...horse.competitionResults,
        event.detail.result,
      ];
      horses = [...horses];
      console.log('Competition result added:', event.detail);
    }
  };

  onMount(async () => {
    try {
      await initAuth();
      console.log('Auth initialized');
      
      isAuthenticated.subscribe(value => {
        console.log('Authentication state changed:', value);
      });
      
      user.subscribe(value => {
        console.log('User state changed:', value);
      });
      
      appReady = true;
    } catch (error) {
      console.error('Error in onMount:', error);
      error = 'Failed to initialize application. Please refresh the page.';
    }
  });
</script>

<style>
  .logo-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  .logo-container img {
    height: 60px;
    width: 60px;
    margin-bottom: 1em;
  }
</style>

{#if error}
  <div class="notification is-danger">
    {error}
  </div>
{/if}

{#if appReady}
  <Router>
    <main>
      <div class="logo-container">
        <img src="/jumping_horse_logo_transparent.png" alt="Jumping Horse Logo" />
        <h1 class="title">Horse Breeding App</h1>
      </div>
      <nav class="navbar is-info">
        <div class="navbar-menu">
          <div class="navbar-start">
            <Link class="navbar-item" to="/">View Horses</Link>
            <Link class="navbar-item" to="/my-horses">My Horses</Link>
            <Link class="navbar-item" to="/match">Match Horses</Link>
            <Link class="navbar-item" to="/auto-match">Auto Match</Link>
            <Link class="navbar-item" to="/one-vs-many">One vs Many</Link>
            <Link class="navbar-item" to="/chat">Chat</Link>
            <Link class="navbar-item" to="/add-horse">Add Horse</Link>
            <Link class="navbar-item" to="/add-competition-result">Add Competition Result</Link>
          </div>
          <div class="navbar-end">
            {#if $isAuthenticated}
              <div class="navbar-item">
                <span>Welcome, {$user?.name || 'User'}</span>
              </div>
              <div class="navbar-item">
                <button 
                  class="button is-light" 
                  on:click={() => {
                    console.log('Logout clicked');
                    logout();
                  }}
                >
                  Logout
                </button>
              </div>
            {:else}
              <div class="navbar-item">
                <button
                  class="button is-light"
                  on:click={() => {
                    console.log('Login clicked');
                    login();
                  }}
                  disabled={!$isAuth0ClientReady}
                >
                  Login
                </button>
              </div>
            {/if}
          </div>
        </div>
      </nav>

      <Route path="/">
        <ViewHorses {horses} {fetchHorses} {isLoading} />
      </Route>
      <Route path="/my-horses">
        {#if $isAuthenticated}
          <MyHorses />
        {:else}
          <p class="has-text-centered">Please log in to view your horses.</p>
        {/if}
      </Route>
      <Route path="/chat">
        {#if $isAuthenticated}
          <Chat />
        {:else}
          <p class="has-text-centered">Please log in to use the chat.</p>
        {/if}
      </Route>
      <Route path="/horses/:selectedHorseId" let:params>
        {#if $isAuthenticated}
          <Horse {params} />
        {:else}
          <p class="has-text-centered">Please log in to view horse details.</p>
        {/if}
      </Route>
      <Route path="/match">
        {#if $isAuthenticated}
          <MatchHorses {horses} />
        {:else}
          <p class="has-text-centered">Please log in to match horses.</p>
        {/if}
      </Route>
      <Route path="/auto-match">
        {#if $isAuthenticated}
          <AutoMatch {horses} />
        {:else}
          <p class="has-text-centered">Please log in to use auto-match.</p>
        {/if}
      </Route>
      <Route path="/one-vs-many">
        {#if $isAuthenticated}
          <OneVsMany {horses} />
        {:else}
          <p class="has-text-centered">Please log in to use one-vs-many matching.</p>
        {/if}
      </Route>
      <Route path="/chat">
        {#if $isAuthenticated}
          <Chat />
        {:else}
          <p class="has-text-centered">Please log in to use the chat.</p>
        {/if}
      </Route>
      <Route path="/add-horse">
        {#if $isAuthenticated}
          <AddHorse {addHorse} />
        {:else}
          <p class="has-text-centered">Please log in to add a horse.</p>
        {/if}
      </Route>
      <Route path="/add-competition-result">
        {#if $isAuthenticated}
          <AddCompetitionResult {addCompetitionResult} />
        {:else}
          <p class="has-text-centered">Please log in to add a competition result.</p>
        {/if}
      </Route>
    </main>
  </Router>
{:else}
  <div class="loading-container">
    <p>Loading application...</p>
    <progress class="progress is-primary" />
  </div>
{/if}
