<script>
  import { Link } from "svelte-routing";
  import { getToken, isAuthenticated } from './auth.js';
  import { onMount } from 'svelte';


  export let horses = [];
  export let page = 1;
  export let per_page = 100;
  export let fetchHorses;
  export let isLoading;
  let error = null;

  // Filters
  let name;
  let sex;
  let min_age;
  let max_age;
  let min_height;
  let max_height;
  let min_jumping_class;
  let breeder;
  let fei;
  let quartile;
  let filters = {};

  let myHorses = []; // store the user's horses
  let toastMessage = null; // For showing temporary success messages

  const applyFilters = () => {
    filters = {};
    if (name) filters.name = name;
    if (sex) filters.sex = sex;
    if (min_age) filters.min_age = min_age;
    if (max_age) filters.max_age = max_age;
    if (min_height) filters.min_height = min_height;
    if (max_height) filters.max_height = max_height;
    if (min_jumping_class) filters.min_jumping_class = min_jumping_class;
    if (breeder) filters.breeder = breeder;
    if (fei) filters.fei = fei;
    if (quartile) filters.quartile = Number(quartile);

    console.log('Applying filters:', filters);
    page = 1; 
    fetchHorses(page, per_page, filters);
  };

  const changePage = (newPage) => {
    if (newPage < 1) return;
    page = newPage;
    fetchHorses(page, per_page, filters);
  };

  const showToast = (message) => {
    toastMessage = message;
    setTimeout(() => {
      toastMessage = null; // clear after 3 seconds
    }, 3000);
  };

  const addToMyHorses = async (horse_id) => {
    try {
      const token = await getToken();
      if (!token) {
        throw new Error('No authentication token available');
      }

      const response = await fetch(
        `http://127.0.0.1:5000/my_horses`,
        {
          method: "POST",
          body: JSON.stringify({ horse_id }),
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          credentials: 'include'
        }
      );

      if (!response.ok) {
        const errorText = await response.text();
        console.error('Error adding horse:', errorText);
        throw new Error("Failed to add horse");
      }

      // Add horse locally
      myHorses = [...myHorses, { HorsetelexID: horse_id }];

      const addedHorse = horses.find(h => h.HorsetelexID === horse_id);
      const horseName = addedHorse ? addedHorse.Name : 'The horse';
      showToast(`${horseName} has been added to your horses.`);
    } catch (err) {
      console.error("Error adding horse:", err);
      error = 'Failed to add horse. Please try again.';
    }
  };

  const removeFromMyHorses = async (horse_id) => {
    try {
      const token = await getToken();
      if (!token) {
        throw new Error('No authentication token available');
      }

      const response = await fetch(`http://127.0.0.1:5000/my_horses/${horse_id}`, {
        method: "DELETE",
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        const errorText = await response.text();
        console.error('Error removing horse:', errorText);
        throw new Error("Failed to remove horse");
      }

      // Remove horse locally
      myHorses = myHorses.filter(h => h.HorsetelexID !== horse_id);

      const removedHorse = horses.find(h => h.HorsetelexID === horse_id);
      const horseName = removedHorse ? removedHorse.Name : 'The horse';
      showToast(`${horseName} has been removed from your horses.`);
    } catch (err) {
      console.error("Error removing horse:", err);
      error = 'Failed to remove horse. Please try again.';
    }
  };

  const fetchMyHorses = async () => {
    try {
      const token = await getToken();
      if (!token) {
        console.log("Not authenticated, cannot fetch my_horses");
        return;
      }

      const response = await fetch(`http://127.0.0.1:5000/my_horses`, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Failed to fetch my horses:", errorText);
        return;
      }

      const data = await response.json();
      myHorses = data.data; 
    } catch (err) {
      console.error("Error fetching my_horses:", err);
    }
  };

  const isHorseInMyHorses = (horse_id) => {
    return myHorses.some(h => h.HorsetelexID === horse_id);
  };

  onMount(() => {
    if ($isAuthenticated) {
      fetchMyHorses();
    }
  });

  $: {
    console.log("Horses in ViewHorses updated:", horses);
  }
</script>

<main>
  {#if !$isAuthenticated}
    <p class="has-text-centered">Please log in to view horses.</p>
  {:else if error}
    <div class="notification is-danger">
      {error}
    </div>
  {:else if isLoading}
    <div class="loading-container">
      <p class="has-text-centered">Loading horses...</p>
      <progress class="progress is-info" max="100"></progress>
    </div>
  {:else if horses.length === 0}
    <div class="empty-state">
      <p class="has-text-centered">No horses found.</p>
    </div>
  {:else}
    <!-- Toast Message -->
    {#if toastMessage}
      <div class="notification is-success">
        {toastMessage}
      </div>
    {/if}

    <!-- Filters -->
    <div class="filters">
      <input type="text" placeholder="Name" bind:value={name} class="input" />
      <select bind:value={sex} class="input">
        <option value="">Sex</option>
        <option value="Mare">Mare</option>
        <option value="Stallion">Stallion</option>
        <option value="Gelding">Gelding</option>
      </select>
      <input type="number" placeholder="Min age" bind:value={min_age} class="input" />
      <input type="number" placeholder="Max age" bind:value={max_age} class="input" />
      <input type="number" placeholder="Min height (m)" bind:value={min_height} class="input" />
      <input type="number" placeholder="Max height (m)" bind:value={max_height} class="input" />
      <input type="number" placeholder="Min jumping class" bind:value={min_jumping_class} class="input" />
      <input type="text" placeholder="Breeder" bind:value={breeder} class="input" />
      <input type="text" placeholder="FEI ID" bind:value={fei} class="input" />
      <select bind:value={quartile} class="input">
        <option value="">Select Score Quartile</option>
        <option value="1">1st Quartile</option>
        <option value="2">2nd Quartile</option>
        <option value="3">3rd Quartile</option>
        <option value="4">4th Quartile</option>
      </select>
      <button class="button is-primary" on:click={applyFilters}>Search</button>
    </div>

    <!-- Horses Table -->
    <table class="table is-fullwidth is-striped">
      <thead>
        <tr>
          <th>Name</th>
          <th>ID</th>
          <th>Sex</th>
          <th>Year</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {#each horses as horse}
          <tr>
            <td><a href={horse['Horsetelex link']} target="_blank">{horse.Name}</a></td>
            <td>{horse.HorsetelexID}</td>
            <td>{horse['Horse Type']}</td>
            <td>{Math.floor(horse['Horse YoB'])}</td>
            <td>
              <div class="buttons">
                <Link class="button is-small is-info" to={`/horses/${horse.HorsetelexID}`}>View</Link>
                {#if isHorseInMyHorses(horse.HorsetelexID)}
                  <button class="button is-small is-danger" on:click={() => removeFromMyHorses(horse.HorsetelexID)}>Remove from My Horses</button>
                {:else}
                  <button class="button is-small is-info" on:click={() => addToMyHorses(horse.HorsetelexID)}>Add to My Horses</button>
                {/if}
              </div>
            </td>
          </tr>
        {/each}
      </tbody>
    </table>

    <!-- Pagination -->
    <nav class="pagination" aria-label="pagination">
      <button class="button" on:click={() => changePage(page - 1)} disabled={page === 1}>Previous</button>
      <button class="button" on:click={() => changePage(page + 1)}>Next</button>
    </nav>
  {/if}
</main>
