<!-- Basic identification by ID:

"Tell me about horse 12345."
"Can you give me the details of horse 98765?"
"What is the composite score of horse 20001?"
Breeding and characteristics by ID:

"What type of horse is horse 30086?"
"When was horse 55555 born?"
"Is horse 10010 a stallion or a mare?"
Asking for general evaluation once a horse is identified by ID:

"Now that we've looked at horse 7777, can you tell me if it has a good composite score?"
"For horse 22222, can you summarize its background and potential as a breeding prospect?"
Queries Related to a Horse by Name:

Single name query:

"Do you have any records for a horse named Secretariat?"
"Tell me about the horse named Snowfall."
"What can you tell me about a horse named Black Beauty?"
Handling multiple matches: If the name is not unique, the system will list multiple horses. You could ask:

"Im looking for a horse named Silver Star."
If it returns multiple matches, you can then ask:
"From the list you provided for Silver Star, please give me more details about horse 455579."
Further details after selecting from multiples:

"You mentioned several horses named Kingdom Come. Tell me more about the one with ID 101010."
Name and ID Together:

Verifying a match:
"Im interested in horse 12345, who I believe is named Stormy Weather. Can you confirm?" If ID and name dont match the same horse, it will inform you.
General / Open-Ended Queries without a Specific Horse: If you dont mention an ID or a name, the context_data remains generic ("You have access to a database of horses."). In that case, the assistant sees you have a database but no particular horse context. You might still ask:

"What kind of data do you have on horses?"
"How do I find a horse by its name?"
"What is a composite score for a horse?"
After Identifying a Specific Horse: Once the context_data is set with a specific horses info (ID, name, year of birth, type, composite score), you can ask follow-up questions:

"Given that horse 160133 is a stallion born in 2010, what would you recommend for improving its breeding line?"
"Is the composite score of horse 55555 considered high among horses in this database?"
"Can you suggest any training regimen based on the type of horse 202020?"
If Multiple Horses Share the Same Name:

Ask for the horse again by specifying the ID after it lists the matches:
"I see multiple horses named Aurora. Could you give me details about the Aurora with ID 30086?"
Error Handling Queries:

"Tell me about horse 99999999" (if this ID doesnt exist, the assistant will say it cant find records).
"Im looking for a horse named RandomNameThatDoesNotExist" (no records found scenario). -->
<script>
  let userMessage = "";
  let conversation = [];
  let isLoading = false;
  let error = null;

  async function sendMessage() {
    if (!userMessage.trim()) return;

    // Add the users message to the conversation
    conversation = [...conversation, { role: 'user', content: userMessage }];
    const messageToSend = userMessage; // store to avoid race conditions
    userMessage = ""; // clear input
    error = null; // reset error
    isLoading = true;

    try {
      const response = await fetch('http://127.0.0.1:5000/chat', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ message: messageToSend })
      });

      isLoading = false;

      if (!response.ok) {
        // If response is not ok, read error
        const errorText = await response.text();
        error = "Error: " + errorText;
        // Optionally add the error message to conversation
        conversation = [...conversation, { role: 'assistant', content: 'Sorry, I ran into an error.' }];
        return;
      }

      const data = await response.json();
      // data.response should contain the assistant's reply
      conversation = [...conversation, { role: 'assistant', content: data.response }];
    } catch (err) {
      isLoading = false;
      error = "Network error: " + err.message;
      conversation = [...conversation, { role: 'assistant', content: 'Sorry, a network error occurred.' }];
    }
  }

  // Allow send on Enter key
  function handleKeyDown(e) {
    if (e.key === 'Enter') {
      e.preventDefault();
      sendMessage();
    }
  }
</script>

<div class="chat-wrapper">
  <h2 class="chat-title">Horse Chat Assistant</h2>
  <div class="chat-container">
    <div class="messages">
      {#if conversation.length === 0 && !isLoading && !error}
        <div class="no-messages">No messages yet. Start by asking about a horse!</div>
      {/if}

      {#each conversation as msg}
        <div class={msg.role === 'user' ? 'message user-msg' : 'message assistant-msg'}>
          <div class="msg-content">{msg.content}</div>
        </div>
      {/each}

      {#if isLoading}
        <div class="message assistant-msg">
          <div class="msg-content">Thinking...</div>
        </div>
      {/if}
    </div>

    {#if error}
      <div class="error">{error}</div>
    {/if}

    <div class="input-bar">
      <input
        type="text"
        bind:value={userMessage}
        placeholder="Ask about horses..."
        on:keydown={handleKeyDown}
      />
      <button on:click={sendMessage} disabled={isLoading}>Send</button>
    </div>
  </div>
</div>

