// src/auth.js
import { createAuth0Client } from '@auth0/auth0-spa-js';
import { writable } from 'svelte/store';

// Stores for authentication status and user information
export const isAuthenticated = writable(false);
export const user = writable(null);
export const auth0Client = writable(null);
export const isAuth0ClientReady = writable(false);

let client = null;

// Initialize the Auth0 client
export async function initAuth() {
  try {
    console.log('Initializing Auth0 client');

    client = await createAuth0Client({
      domain: 'dev-3ivgmqrieljfq0g2.us.auth0.com',
      clientId: 's1h0Ekd4prMP7OxEyoTnaSjIKuCXmBqa',
      // authorizationParams: { // deployment
      //   redirect_uri: 'https://flask-horses-ui.onrender.com',
      //   audience: 'https://horsebreeding/api',
      //   scope: 'openid profile email'
      // }
      // deployment
      authorizationParams: {
        redirect_uri: 'http://localhost:8080',
        audience: 'https://horsebreeding/api',
        scope: 'openid profile email'
      }
    });

    auth0Client.set(client);
    isAuth0ClientReady.set(true);

    try {
      // Handle authentication if returning from Auth0 redirect
      if (
        window.location.search.includes('code=') &&
        window.location.search.includes('state=')
      ) {
        await client.handleRedirectCallback();
        window.history.replaceState(
          {},
          document.title,
          window.location.pathname
        );
      }

      // Check if the user is authenticated
      const authed = await client.isAuthenticated();
      isAuthenticated.set(authed);

      if (authed) {
        const userProfile = await client.getUser();
        user.set(userProfile);
        console.log('User is authenticated:', userProfile);
      }
    } catch (error) {
      console.error('Error handling authentication:', error);
      isAuthenticated.set(false);
      user.set(null);
    }
  } catch (error) {
    console.error('Error initializing Auth0:', error);
    throw error;
  }
}

export async function login() {
  try {
    await client.loginWithRedirect({
      authorizationParams: {
        redirect_uri: 'http://localhost:8080'
        // redirect_uri: 'https://flask-horses-ui.onrender.com' // deployment
      }
    });
  } catch (error) {
    console.error('Login error:', error);
  }
}

export async function logout() {
  try {
    await client.logout({
      logoutParams: {
        // returnTo: 'https://flask-horses-ui.onrender.com' // deployment
        returnTo: 'http://localhost:8080'
      }
    });
    isAuthenticated.set(false);
    user.set(null);
  } catch (error) {
    console.error('Logout error:', error);
  }
}

export async function getToken() {
  if (!client) {
    console.error('Auth0 client is not initialized');
    return null;
  }
  try {
    console.log('Getting token...');
    const token = await client.getTokenSilently({
      audience: 'https://horsebreeding/api'
    });
    console.log('Token received:', token.substring(0, 10) + '...');
    return token;
  } catch (error) {
    console.error('Error getting token:', error);
    // Could set a global error store or just return null
    return null;
  }
}
