<script>
  import { onMount, onDestroy } from "svelte";
  import { Link } from "svelte-routing";
  import { getToken, isAuthenticated } from './auth.js';
  import { navigate } from 'svelte-routing';
  import HorseAncestryTree from './HorseAncestryTable.svelte';
  import HorseAncestryTable from "./HorseAncestryTable.svelte";

  export let params;

  let selectedHorseId = params && params.selectedHorseId ? +params.selectedHorseId : undefined;

  let selectedHorse;
  let horseAncestry = null;
  let isHorseLoading = true;
  let isHorseButtonLoading = false;
  let myHorses = [];
  let isMyHorsesLoading = true;
  let isHorseInMyHorses = false;
  let error = null;
  let loadingProgress = 0;
  let loadingInterval;

  const getCompositeScoreQuartile = (score) => {
    if (score < 438.033669) return 1;
    if (score < 484.512003) return 2;
    if (score < 519.720959) return 3;
    return 4;
  };

  const getCompetitionScoreQuartile = (score) => {
    if (score < 385.714286) return 1;
    if (score < 450.0) return 2;
    if (score < 520.0) return 3;
    return 4;
  };

  const horseInMyHorses = (horseId) => {
    return myHorses.some(({ HorsetelexID }) => HorsetelexID === horseId);
  };

  const fetchHorse = async () => {
    try {
      isHorseLoading = true;
      error = null;
      loadingProgress = 0;

      loadingInterval = setInterval(() => {
        loadingProgress = Math.min(95, loadingProgress + 5);
      }, 100);

      const token = await getToken();
      if (!token) {
        throw new Error('No authentication token available');
      }

      const response = await fetch(`http://127.0.0.1:5000/horses/${selectedHorseId}`, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        throw new Error("Failed to fetch horse");
      }

      selectedHorse = await response.json();
      isHorseInMyHorses = !!selectedHorse && horseInMyHorses(Math.floor(selectedHorse.HorsetelexID));

      loadingProgress = 100; 
    } catch (err) {
      console.error("Error fetching horse:", err);
      error = 'Failed to fetch horse details. Please try again.';
    } finally {
      clearInterval(loadingInterval);
      setTimeout(() => {
        isHorseLoading = false;
      }, 200);
    }
  };

  const fetchMyHorses = async () => {
    try {
      isMyHorsesLoading = true;
      const token = await getToken();

      const response = await fetch(`http://127.0.0.1:5000/my_horses`, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        throw new Error("Failed to fetch horses");
      }

      const horseData = await response.json();
      myHorses = horseData.data;

      if (selectedHorse) {
        isHorseInMyHorses = horseInMyHorses(Math.floor(selectedHorse.HorsetelexID));
      }
    } catch (err) {
      console.error("Error fetching horses:", err);
      error = 'Failed to fetch horses list. Please try again.';
    } finally {
      isMyHorsesLoading = false;
    }
  };

  const removeFromMyHorses = async (horse_id) => {
    try {
      isHorseButtonLoading = true;
      const token = await getToken();
      
      const response = await fetch(`http://127.0.0.1:5000/my_horses/${horse_id}`, {
        method: "DELETE",
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        throw new Error("Failed to delete horse");
      }

      myHorses = myHorses.filter(({ HorsetelexID }) => HorsetelexID !== horse_id);
      isHorseInMyHorses = false;
    } catch (err) {
      console.error("Error deleting horse:", err);
      error = 'Failed to remove horse. Please try again.';
    } finally {
      isHorseButtonLoading = false;
    }
  };

  const addToMyHorses = async (horse_id) => {
    try {
      isHorseButtonLoading = true;
      const token = await getToken();

      const response = await fetch(`http://127.0.0.1:5000/my_horses`, {
        method: "POST",
        body: JSON.stringify({ horse_id }),
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        throw new Error("Failed to add horse");
      }

      myHorses = [...myHorses, selectedHorse];
      isHorseInMyHorses = true;
    } catch (err) {
      console.error("Error adding horse:", err);
      error = 'Failed to add horse. Please try again.';
    } finally {
      isHorseButtonLoading = false;
    }
  };

  // Fetch ancestry data
  const fetchHorseAncestry = async () => {
    try {
      const token = await getToken();
      if (!token) return;
      
      const response = await fetch(`http://127.0.0.1:5000/horses/${selectedHorseId}/ancestry`, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        // If no ancestry data found, we just don't show the tree
        console.log("No ancestry data or failed to fetch ancestry");
        return;
      }

      horseAncestry = await response.json();
      console.log("Fetched horseAncestry:", horseAncestry);
    } catch (err) {
      console.error("Error fetching ancestry:", err);
      // Not critical, just means no ancestry data
    }
  };

  function handleHorseClick(event) {
    const clickedHorse = event.detail;
    if (clickedHorse && clickedHorse.id) {
      navigate(`/horses/${clickedHorse.id}`);
    }
  }

  onMount(() => {
    if ($isAuthenticated && selectedHorseId) {
      fetchHorse().then(() => {
        // After horse details are fetched, try to fetch ancestry
        if (!error) {
          fetchHorseAncestry();
        }
      });
      fetchMyHorses();
    }
  });

  onDestroy(() => {
    if (loadingInterval) clearInterval(loadingInterval);
  });
</script>

<main>
  {#if !$isAuthenticated}
    <p class="has-text-centered">Please log in to view horse details.</p>
  {:else if error}
    <div class="notification is-danger">
      {error}
    </div>
  {:else if isHorseLoading || !selectedHorse}
    <div class="loading-container">
      <h2 class="subtitle has-text-centered mb-4">Loading Horse Details...</h2>
      <progress 
        class="progress is-info" 
        value={loadingProgress} 
        max="100"
      >
        {loadingProgress}%
      </progress>
    </div>
  {:else}
    <div class="box">
      <h2 class="subtitle">{selectedHorse.Name}</h2>
      <p><strong>ID</strong> <a href={selectedHorse['Horsetelex link']} target="_blank">{Math.floor(selectedHorse.HorsetelexID)}</a></p>
      <p><strong>Sex</strong> {selectedHorse['Horse Type']}</p>
      <p><strong>Year</strong> {selectedHorse['Horse YoB']}</p>

      <p><strong>Height:</strong> {selectedHorse['Horse Height'] === "null" ? "N/A" : selectedHorse['Horse Height']}</p>
      <p><strong>Color:</strong> {selectedHorse['Horse Color']}</p>
      <p><strong>In Studbooks:</strong> {selectedHorse['Studbook IDs json']}</p>

      <p><strong>Sire:</strong>
        {#if selectedHorse['father_id'] === "null"}
          N/A
        {:else}
          <Link to={`/horses/${Math.floor(selectedHorse['father_id'])}`}>
            {Math.floor(selectedHorse['father_id'])}
          </Link>
        {/if}
      </p>
      <p><strong>Dame:</strong>
        {#if selectedHorse['mother_id'] === "null"}
          N/A
        {:else}
          <Link to={`/horses/${Math.floor(selectedHorse['mother_id'])}`}>
            {Math.floor(selectedHorse['mother_id'])}
          </Link>
        {/if}
      </p>
      <p><strong>Jumping Class</strong> {selectedHorse['Jumping Class'] === "null" ? "N/A" : selectedHorse['Jumping Class']}</p>
      
      <p><strong>Quality quartile</strong> {selectedHorse['composite_score'] === "null" ? "N/A" : getCompositeScoreQuartile(selectedHorse['composite_score'])}</p>
      <p><strong>Father quality quartile</strong> {selectedHorse['father_composite_score']  === "null" ? "N/A" : getCompositeScoreQuartile(selectedHorse['father_composite_score'])}</p>
      <p><strong>Mother quality quartile</strong> {selectedHorse['mother_composite_score']  === "null" ? "N/A" : getCompositeScoreQuartile(selectedHorse['mother_composite_score'])}</p>
      <p><strong>Inbreeding percentage:</strong> {selectedHorse['Inbreeding Coefficient (%)'] === "null" ? "N/A" : selectedHorse['Inbreeding Coefficient (%)']}</p>
      <p><strong>Ancestry score</strong> {selectedHorse['ancestry_score'] === "null" ? "N/A" : selectedHorse['ancestry_score']}</p>

      {#if isHorseInMyHorses}
        <button 
          class="button is-danger {isHorseButtonLoading ? 'is-loading' : ''}"
          disabled={isHorseButtonLoading}
          style="margin-top: 1em;"
          on:click={() => removeFromMyHorses(Math.floor(selectedHorse.HorsetelexID))}
        >
          Remove from My Horses
        </button>
      {:else}
        <button 
          class="button is-primary {isHorseButtonLoading ? 'is-loading' : ''}"
          style="margin-top: 1em;"
          on:click={() => addToMyHorses(Math.floor(selectedHorse.HorsetelexID))}
          disabled={isHorseButtonLoading}
        >
          Add to My Horses
        </button>
      {/if}

    </div>

    <div class="box" style="margin-top: 2em;">
      <h3 class="subtitle">Competition Results</h3>
      <table class="table is-fullwidth is-striped">
        <thead>
          <tr>
            <th>Date</th>
            <th>City/Show</th>
            <th>Country</th>
            <th>Discipline</th>
            <th>Type</th>
            <th>Obstacle height</th>
            <th>Comp. Nr.</th>
            <th>Rank</th>
            <th>Result</th>
            <th>Price</th>
            <th>Competition score</th>
          </tr>
        </thead>
        <tbody>
          {#each selectedHorse.results as result}
            <tr>
              <td>{result['Date']}</td>
              <td>{result['City / Show']}</td>
              <td>{result['Country code']}</td>
              <td>{result['Discipline']}</td>
              <td>{result['Type']}</td>
              <td>{result['Obstacle Height']}</td>
              <td>{result['Competition number']}</td>
              <td>{result['Rank']}</td>
              <td>{result['Result']}</td>
              <td>{result['Price']}</td>
              <td>{`${Math.round(result['competition_score'], 2)}, quartile ${getCompetitionScoreQuartile(result['competition_score'])}`}</td>
            </tr>
          {/each}
        </tbody>
      </table>
    </div>

{#if horseAncestry}
  <div class="box" style="margin-top: 2em;">
    <h3 class="subtitle">Ancestry</h3>
    <HorseAncestryTable {horseAncestry}/>
  </div>
{/if}

    <!-- {#if horseAncestry}
    {#await Promise.resolve() then}
      {console.log('Rendering HorseAncestryTree with', horseAncestry)}
      <div class="box" style="margin-top: 2em;">
        <h3 class="subtitle">Ancestry</h3>
        <HorseAncestryTree {horseAncestry} on:horseclick={handleHorseClick} />
      </div>
    {/await}
  {/if} -->
  {/if}
</main>
