<script>
  import { createEventDispatcher } from "svelte";

  const dispatch = createEventDispatcher();
  let horse = {
    id: Date.now(),
    name: "",
    alias1: "",
    alias2: "",
    alias3: "",
    sex: "",
    year: "",
    sire: null,
    dam: null,
    studbook: "",
    lifeNumber: "",
    color: "",
    height: "",
    reproduction: "",
    inbreedingPercentage: "",
    competitionResults: [],
  };

  const addHorse = () => {
    console.log("Dispatching add event with horse:", horse);
    dispatch("add", horse);
    // Reset form
    horse = {
      id: Date.now(),
      name: "",
      alias1: "",
      alias2: "",
      alias3: "",
      sex: "",
      year: "",
      sire: null,
      dam: null,
      studbook: "",
      lifeNumber: "",
      color: "",
      height: "",
      reproduction: "",
      inbreedingPercentage: "",
      competitionResults: [],
    };
  };
</script>

<section class="section">
  <div class="container">
    <h1 class="title">Add New Horse</h1>
    <div class="card">
      <div class="card-content">
        <form on:submit|preventDefault={addHorse}>
          <!-- Name -->
          <div class="field">
            <label class="label" for="name">Name<span class="has-text-danger">*</span></label>
            <div class="control">
              <input class="input" type="text" id="name" bind:value={horse.name} required placeholder="Enter horse name" />
            </div>
          </div>

          <!-- Alias 1 -->
          <div class="field">
            <label class="label" for="alias1">Alias 1</label>
            <div class="control">
              <input class="input" type="text" id="alias1" bind:value={horse.alias1} placeholder="Optional alias" />
            </div>
            <p class="help">If the horse is known by another name</p>
          </div>

          <!-- Alias 2 -->
          <div class="field">
            <label class="label" for="alias2">Alias 2</label>
            <div class="control">
              <input class="input" type="text" id="alias2" bind:value={horse.alias2} placeholder="Optional alias" />
            </div>
          </div>

          <!-- Alias 3 -->
          <div class="field">
            <label class="label" for="alias3">Alias 3</label>
            <div class="control">
              <input class="input" type="text" id="alias3" bind:value={horse.alias3} placeholder="Optional alias" />
            </div>
          </div>

          <!-- Sex -->
          <div class="field">
            <label class="label" for="sex">Sex<span class="has-text-danger">*</span></label>
            <div class="control">
              <div class="select is-fullwidth">
                <select id="sex" bind:value={horse.sex} required>
                  <option value="">Select sex</option>
                  <option value="Stallion">Stallion</option>
                  <option value="Gelding">Gelding</option>
                  <option value="Mare">Mare</option>
                </select>
              </div>
            </div>
          </div>

          <!-- Year -->
          <div class="field">
            <label class="label" for="year">Year<span class="has-text-danger">*</span></label>
            <div class="control">
              <input class="input" type="number" id="year" bind:value={horse.year} required placeholder="e.g. 2015" />
            </div>
          </div>

          <!-- Sire -->
          <div class="field">
            <label class="label" for="sire">Sire</label>
            <div class="control">
              <input class="input" type="text" id="sire" bind:value={horse.sire} placeholder="Sire's name (if known)" />
            </div>
          </div>

          <!-- Dam -->
          <div class="field">
            <label class="label" for="dam">Dam</label>
            <div class="control">
              <input class="input" type="text" id="dam" bind:value={horse.dam} placeholder="Dam's name (if known)" />
            </div>
          </div>

          <!-- Studbook -->
          <div class="field">
            <label class="label" for="studbook">Studbook</label>
            <div class="control">
              <input class="input" type="text" id="studbook" bind:value={horse.studbook} placeholder="e.g. KWPN, BWP" />
            </div>
          </div>

          <!-- Life Number -->
          <div class="field">
            <label class="label" for="lifeNumber">Life Number</label>
            <div class="control">
              <input class="input" type="text" id="lifeNumber" bind:value={horse.lifeNumber} placeholder="Unique life number if applicable" />
            </div>
          </div>

          <!-- Color -->
          <div class="field">
            <label class="label" for="color">Color</label>
            <div class="control">
              <div class="select is-fullwidth">
                <select id="color" bind:value={horse.color}>
                  <option value="">Select color</option>
                  <option value="Black">Black</option>
                  <option value="Bay">Bay</option>
                  <option value="Chestnut">Chestnut</option>
                  <!-- Add other colors as needed -->
                </select>
              </div>
            </div>
          </div>

          <!-- Height -->
          <div class="field">
            <label class="label" for="height">Height (cm)<span class="has-text-danger">*</span></label>
            <div class="control">
              <input class="input" type="number" id="height" bind:value={horse.height} required placeholder="e.g. 165" />
            </div>
          </div>

          <!-- Reproduction -->
          <div class="field">
            <label class="label" for="reproduction">Reproduction</label>
            <div class="control">
              <div class="select is-fullwidth">
                <select id="reproduction" bind:value={horse.reproduction}>
                  <option value="">Select reproduction method</option>
                  <option value="Normal insemination">Normal insemination</option>
                  <option value="Embryo transfer">Embryo transfer</option>
                  <option value="Clone">Clone</option>
                  <option value="Twins">Twins</option>
                </select>
              </div>
            </div>
          </div>

          <!-- Inbreeding Percentage -->
          <div class="field">
            <label class="label" for="inbreedingPercentage">Inbreeding Percentage<span class="has-text-danger">*</span></label>
            <div class="control">
              <input
                class="input"
                type="number"
                id="inbreedingPercentage"
                step="0.01"
                bind:value={horse.inbreedingPercentage}
                required
                placeholder="e.g. 2.50"
              />
            </div>
            <p class="help">Use a decimal value, for example 2.50 for 2.5%</p>
          </div>

          <!-- Submit Button -->
          <div class="field mt-5">
            <div class="control">
              <button type="submit" class="button is-primary is-fullwidth">Add Horse</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</section>

<style>
  .title {
    margin-bottom: 1rem;
  }
  .card {
    margin-top: 1rem;
  }
</style>
