<script>
  import { getToken, isAuthenticated } from './auth.js';

  export let horses = [];
  let selectedHorse = null;
  let potentialMatches = [];
  let selectedMatches = [];
  let matches = [];
  let isLoading = false;
  let error = null;

  $: {
    if (selectedHorse) {
      // Filter potential matches based on horse type
      potentialMatches = horses.filter(h => {
        const selectedType = selectedHorse['Horse Type'];
        const matchType = h['Horse Type'];
        return (
          (selectedType === 'Stallion' && matchType === 'Mare') ||
          (selectedType === 'Mare' && matchType === 'Stallion')
        );
      });
    } else {
      potentialMatches = [];
    }
  }

  function toggleMatchSelection(horse) {
    const index = selectedMatches.findIndex(h => h.HorsetelexID === horse.HorsetelexID);
    if (index === -1) {
      selectedMatches = [...selectedMatches, horse];
    } else {
      selectedMatches = selectedMatches.filter(h => h.HorsetelexID !== horse.HorsetelexID);
    }
  }

  async function calculateMatches() {
    if (!selectedHorse || selectedMatches.length === 0) return;

    try {
      isLoading = true;
      error = null;
      matches = [];

      const token = await getToken();
      if (!token) {
        throw new Error('No authentication token available');
      }

      // Calculate matches for each selected horse
      const matchPromises = selectedMatches.map(async match => {
        const response = await fetch(
          `http://127.0.0.1:5000/match-pair`,
          {
            method: 'POST',
            body: JSON.stringify({
              stallion_id: selectedHorse['Horse Type'] === 'Stallion' ? selectedHorse.HorsetelexID : match.HorsetelexID,
              mare_id: selectedHorse['Horse Type'] === 'Mare' ? selectedHorse.HorsetelexID : match.HorsetelexID
            }),
            headers: {
              'Authorization': `Bearer ${token}`,
              'Content-Type': 'application/json'
            },
            credentials: 'include'
          }
        );

        if (!response.ok) {
          throw new Error(`Failed to calculate match for horse ${match.Name}`);
        }

        return await response.json();
      });

      const results = await Promise.all(matchPromises);
      displayResults(results);
    } catch (err) {
      console.error("Error calculating matches:", err);
      error = 'Failed to calculate matches. Please try again.';
    } finally {
      isLoading = false;
    }
  }

  function displayResults(results) {
    matches = results.map(result => {
      const matchHorse = selectedHorse['Horse Type'] === 'Stallion' ? result.mare : result.stallion;
      const stallionSummary = result.stallion_summary;
      const mareSummary = result.mare_summary;

      let matchQuality;
      let color;
      
      // Calculate match quality based on summaries and common ancestors
      const hasGoodAncestry = result.common_ancestors.some(a => a.median_composite_score > 450);
      const bothGoodOffspring = stallionSummary.jumping_score_adults_quartile >= 3 && 
                               mareSummary.jumping_score_adults_quartile >= 3;

      if (bothGoodOffspring && hasGoodAncestry) {
        matchQuality = "Excellent";
        color = "is-primary";
      } else if (bothGoodOffspring) {
        matchQuality = "Good";
        color = "is-success";
      } else if (hasGoodAncestry) {
        matchQuality = "Fair";
        color = "is-warning";
      } else {
        matchQuality = "Poor";
        color = "is-danger";
      }

      return {
        name: matchHorse.Name,
        id: matchHorse.HorsetelexID,
        result: matchQuality,
        color,
        stallionQuartile: stallionSummary.jumping_score_adults_quartile,
        mareQuartile: mareSummary.jumping_score_adults_quartile,
        commonAncestors: result.common_ancestors.length
      };
    });

    // Sort matches by quality
    const qualityOrder = { "Excellent": 4, "Good": 3, "Fair": 2, "Poor": 1 };
    matches.sort((a, b) => qualityOrder[b.result] - qualityOrder[a.result]);
  }
</script>

<main>
  {#if !$isAuthenticated}
    <p class="has-text-centered">Please log in to use the matching system.</p>
  {:else}
    <h1 class="title">One vs Many Matching</h1>

    {#if error}
      <div class="notification is-danger">
        {error}
      </div>
    {/if}

    <div class="field">
      <label class="label" for="horse-select">Select a Horse:</label>
      <div class="control">
        <div class="select is-fullwidth">
          <select id="horse-select" bind:value={selectedHorse}>
            <option value={null}>Select a horse</option>
            {#each horses as horse}
              <option value={horse}>{horse.Name} ({horse['Horse Type']})</option>
            {/each}
          </select>
        </div>
      </div>
    </div>
    
    {#if selectedHorse}
      <div class="box">
        <h2 class="subtitle">Selected Horse: {selectedHorse.Name}</h2>
        <p><strong>Type:</strong> {selectedHorse['Horse Type']}</p>
        <p><strong>Year of Birth:</strong> {selectedHorse['Horse YoB']}</p>
        <p><strong>ID:</strong> {selectedHorse.HorsetelexID}</p>
      </div>

      <div class="box">
        <h2 class="subtitle">Select Potential Matches:</h2>
        <div class="columns is-multiline">
          {#each potentialMatches as horse}
            <div class="column is-one-third">
              <label class="checkbox">
                <input
                  type="checkbox"
                  on:change={() => toggleMatchSelection(horse)}
                />
                {horse.Name} ({horse.HorsetelexID})
              </label>
            </div>
          {/each}
        </div>
      </div>

      <div class="field">
        <div class="control">
          <button
            class="button is-primary is-fullwidth"
            on:click={calculateMatches}
            disabled={isLoading || selectedMatches.length === 0}
          >
            {isLoading ? "Calculating Matches..." : "Calculate Matches"}
          </button>
        </div>
      </div>

      {#if isLoading}
        <div class="mt-4">
          <progress class="progress is-primary" />
        </div>
      {/if}

      {#if matches.length > 0 && !isLoading}
        <div class="card mt-4">
          <header class="card-header">
            <p class="card-header-title">
              Match Results for {selectedHorse.Name}
            </p>
          </header>
          <div class="card-content">
            <div class="content">
              <table class="table is-fullwidth is-striped">
                <thead>
                  <tr>
                    <th>Potential Match</th>
                    <th>Result</th>
                    <th>Match ID</th>
                    <th>Common Ancestors</th>
                  </tr>
                </thead>
                <tbody>
                  {#each matches as match}
                    <tr>
                      <td>{match.name}</td>
                      <td>
                        <span class={`tag ${match.color}`}>{match.result}</span>
                      </td>
                      <td>{match.id}</td>
                      <td>{match.commonAncestors}</td>
                    </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      {/if}
    {/if}
  {/if}
</main>