<script>
  import { Link } from "svelte-routing";
  import { onMount } from "svelte";
  import { getToken, isAuthenticated } from './auth.js';

  let horses = [];
  let page = 1;
  let per_page = 100;
  let isLoading = false;
  let error = null;
  let filters = {};

  // Filters
  let name;
  let sex;
  let min_age;
  let max_age;
  let min_height;
  let max_height;
  let min_jumping_class;
  let breeder;
  let fei;
  let quality;

  const applyFilters = () => {
    filters = {};
    if (name) filters.name = name;
    if (sex) filters.sex = sex;
    if (min_age) filters.min_age = min_age;
    if (max_age) filters.max_age = max_age;
    if (min_height) filters.min_height = min_height;
    if (max_height) filters.max_height = max_height;
    if (min_jumping_class) filters.min_jumping_class = min_jumping_class;
    if (breeder) filters.breeder = breeder;
    if (fei) filters.fei = fei;
    if (quality) filters.quality = quality;
    
    console.log('Applying filters:', filters);
    page = 1; // Reset to first page when applying filters
    fetchMyHorses(page, per_page, filters);
  };

  const changePage = (newPage) => {
    if (newPage < 1) return;
    page = newPage;
    fetchMyHorses(page, per_page, filters);
  };

  const removeHorse = async (horseId) => {
  try {
    const token = await getToken();
    if (!token) {
      throw new Error('No authentication token available');
    }

    const response = await fetch(`http://127.0.0.1:5000/my_horses/${horseId}`, {
      method: 'DELETE',
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      },
      credentials: 'include'  // Add this line
    });

    if (!response.ok) {
      console.error('Response status:', response.status);
      console.error('Response status text:', response.statusText);
      const errorText = await response.text();
      console.error('Error response:', errorText);
      throw new Error('Failed to remove horse');
    }

    // Refresh the horses list
    fetchMyHorses(page, per_page, filters);
  } catch (error) {
    console.error('Error removing horse:', error);
    error = 'Failed to remove horse. Please try again.';
  }
};

const fetchMyHorses = async (page, per_page, params = {}) => {
  if (isLoading) return;

  try {
    isLoading = true;
    error = null;

    const token = await getToken();
    if (!token) {
      // Token retrieval failed
      error = 'Session expired or not authenticated. Please log in again.';
      // Optionally prompt re-login or just show the error
      return; // Stop here, don't attempt the fetch
    }

    const paramsString = Object.entries(params)
      .reduce((prev, [key, value]) => `${prev}&${key}=${value}`, '');

    console.log('Fetching horses with token:', token.substring(0, 10) + '...');

    const response = await fetch(
      `http://127.0.0.1:5000/my_horses?page=${page}&per_page=${per_page}${paramsString}`,
      {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      }
    );

    if (!response.ok) {
      console.error('Response status:', response.status);
      console.error('Response status text:', response.statusText);
      const errorText = await response.text();
      console.error('Error response:', errorText);
      throw new Error('Failed to fetch horses');
    }

    const horseData = await response.json();
    horses = horseData.data;
    console.log('Fetched horses:', horses);
  } catch (error) {
    console.error('Error fetching horses:', error);
    error = 'Failed to fetch horses. Please try again.';
    horses = [];
  } finally {
    isLoading = false;
  }
};


// Also update the onMount to check auth state
onMount(async () => {
  console.log('MyHorses component mounted');
  if ($isAuthenticated) {
  fetchMyHorses(page, per_page);
} else {
  console.log('User not authenticated');
  error = 'Please log in to view your horses.';
}
});


  $: {
    console.log("Horses in MyHorses updated:", horses);
  }
</script>

<main>
  <h1 class="title">My Horses</h1>

  {#if error}
    <div class="notification is-danger">
      {error}
    </div>
  {/if}

  {#if $isAuthenticated}
    <!-- Search Bar and Filters -->
    <div class="filters">
      <input
        type="text"
        placeholder="Search by name"
        bind:value={name}
        class="input"
      />

      <select bind:value={sex} class="input">
        <option value="">Sex</option>
        <option value="Mare">Mare</option>
        <option value="Stallion">Stallion</option>
        <option value="Gelding">Gelding</option>
      </select>

      <input
        type="text"
        placeholder="Search by breeder"
        bind:value={breeder}
        class="input"
      />

      <input
        type="text"
        placeholder="Search by FEI ID"
        bind:value={fei}
        class="input"
      />

      <input
        type="number"
        placeholder="Min age"
        bind:value={min_age}
        class="input"
      />

      <input
        type="number"
        placeholder="Max age"
        bind:value={max_age}
        class="input"
      />

      <input
        type="number"
        placeholder="Min height (m)"
        bind:value={min_height}
        class="input"
      />

      <input
        type="number"
        placeholder="Min jumping class"
        bind:value={min_jumping_class}
        class="input"
      />

      <select bind:value={quality} class="input">
        <option value="">Select Score Quartile</option>
        <option value="1">1st Quartile</option>
        <option value="2">2nd Quartile</option>
        <option value="3">3rd Quartile</option>
        <option value="4">4th Quartile</option>
      </select>

      <button class="button is-primary" on:click={applyFilters}>
        Search
      </button>
    </div>

    {#if isLoading}
      <p class="has-text-centered">Loading horses...</p>
    {:else if horses.length === 0}
      <p class="has-text-centered">No horses in your collection yet.</p>
    {:else}
      <table class="table is-fullwidth is-striped">
        <thead>
          <tr>
            <th>Name</th>
            <th>ID</th>
            <th>Sex</th>
            <th>Year</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {#each horses as horse (horse.HorsetelexID)}
            <tr>
              <td>
                <a href={horse['Horsetelex link']} target="_blank">{horse.Name}</a>
              </td>
              <td>{horse.HorsetelexID}</td>
              <td>{horse['Horse Type']}</td>
              <td>{Math.floor(horse['Horse YoB'])}</td>
              <td>
                <div class="buttons">
                  <button class="button is-small is-info">
                    <Link to={`/horses/${horse.HorsetelexID}`}>View</Link>
                  </button>
                  <button 
                    class="button is-small is-danger"
                    on:click={() => removeHorse(horse.HorsetelexID)}
                  >
                    Remove
                  </button>
                </div>
              </td>
            </tr>
          {/each}
        </tbody>
      </table>

      <!-- Pagination -->
      <nav class="pagination" aria-label="pagination">
        <button 
          class="button" 
          on:click={() => changePage(page - 1)} 
          disabled={page === 1}
        >
          Previous
        </button>
        <button 
          class="button" 
          on:click={() => changePage(page + 1)}
        >
          Next
        </button>
      </nav>
    {/if}
  {:else}
    <p class="has-text-centered">Please log in to view your horses.</p>
  {/if}
</main>