<script>
  import { getToken, isAuthenticated } from './auth.js';
  import { onMount } from 'svelte';

  let selectedHorseId;
  let isLoading = false;        // Unified loading state for both fetching horses and calculating matches
  let progress = 0;
  let matches = [];
  let res;
  let error = null;

  let allHorses = [];

  function handleSelectChange() {
    // Reset matches whenever the user picks a new horse
    matches = [];
  }

  onMount(async () => {
    if ($isAuthenticated) {
      isLoading = true;                 // Start loading before fetching horses
      await fetchAllHorses();
      isLoading = false;                // Done fetching horses
    }
  });

  /**
   * A basic CSV parser that assumes:
   * 1) The first line is a header row.
   * 2) Fields are comma-separated with no quoted commas inside fields.
   * 
   * Replace this with a more robust parser if your CSV is more complex.
   */
   function parseCSV(csvText) {
    const lines = csvText.trim().split('\n');
    if (lines.length < 2) return [];  // No data or only header

    // The first line is the header
    const headers = lines[0].split(',');

    // Process each subsequent line into an object
    return lines.slice(1).map(line => {
      const cols = line.split(',');
      let rowObj = {};
      headers.forEach((header, index) => {
        rowObj[header.trim()] = (cols[index] || '').trim();
      });
      return rowObj;
    });
  }

  async function fetchAllHorses() {
    try {
      // Fetch the CSV file from the relative path
      const response = await fetch('/common_ids.csv');
      if (!response.ok) {
        throw new Error(`Failed to load CSV file. Status: ${response.status}`);
      }
      
      // Read the entire CSV as text
      const csvText = await response.text();

      // Parse CSV text into a structured JS array/object
      allHorses = parseCSV(csvText);

      console.log("Parsed CSV horses:", allHorses);
    } catch (err) {
      console.error("Error fetching horses from CSV:", err);
      error = "Failed to fetch horses from CSV.";
    }

    // try {
    //   const token = await getToken();
    //   if (!token) {
    //     console.log("Not authenticated, cannot fetch horses");
    //     return;
    //   }

    //   const response = await fetch(`http://127.0.0.1:5000/horses?page=1&per_page=9999`, {
    //     headers: {
    //       'Authorization': `Bearer ${token}`,
    //       'Content-Type': 'application/json'
    //     },
    //     credentials: 'include'
    //   });

    //   if (!response.ok) {
    //     const errorText = await response.text();
    //     console.error("Failed to fetch horses:", errorText);
    //     return;
    //   }

    //   const data = await response.json();
    //   allHorses = data.data;
    //   console.log("Fetched all horses:", allHorses.length);
    // } catch (err) {
    //   console.error("Error fetching horses:", err);
    // }
  }

  const calculateMatches = async () => {
    if (!selectedHorseId) {
      matches = null;
      return;
    }

    try {
      isLoading = true;              // Indicate loading while calculating matches
      error = null;

      const token = await getToken();
      if (!token) {
        throw new Error('No authentication token available');
      }

      const hID = Number(selectedHorseId);
      const response = await fetch(
        `http://127.0.0.1:5000/find-matches`,
        {
          method: "POST",
          body: JSON.stringify({ horse_id: hID }),
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          credentials: 'include'
        }
      );

      if (!response.ok) {
        console.error('Response status:', response.status);
        console.error('Response status text:', response.statusText);
        const errorText = await response.text();
        console.error('Error response:', errorText);
        throw new Error("Failed to get matches");
      }

      res = await response.json();
      matches = res.matches;
    } catch (err) {
      console.error("Error fetching matches:", err);
      error = 'Failed to calculate matches. Please try again.';
      matches = [];
    } finally {
      isLoading = false;    // Matches calculation complete
    }
  }

</script>

<main>
  {#if !$isAuthenticated}
    <p class="has-text-centered">Please log in to find matches.</p>
  {:else if error}
    <div class="notification is-danger">
      {error}
    </div>
  {:else if isLoading}
    <!-- This matches the loading logic from ViewHorses -->
    <div class="loading-container">
      <p class="has-text-centered">Loading data...</p>
      <progress class="progress is-info" max="100"></progress>
    </div>
  {:else if allHorses.length === 0}
    <div class="empty-state">
      <p class="has-text-centered">No horses found.</p>
    </div>
  {:else}
    <!-- Once not loading and horses are fetched, show the dropdown -->
    <h1 class="title">Find Best Matches</h1>

    <div class="field">
      <label class="label" for="horse-id">Select a Horse:</label>
      <div class="control">
        <div class="select is-fullwidth">
          <select bind:value={selectedHorseId} on:change={handleSelectChange}>
            <option value="">-- Select a Horse --</option>
            {#each allHorses as h}
              <option value={h.HorsetelexID}>{h.Name} (ID: {h.HorsetelexID})</option>
            {/each}
          </select>
        </div>
      </div>
    </div>

    {#if selectedHorseId}
      <div class="field">
        <div class="control">
          <button
            class="button is-primary is-fullwidth"
            on:click={calculateMatches}
            disabled={isLoading}
          >
            {isLoading ? "Calculating Matches..." : "Find Best Matches"}
          </button>
        </div>
      </div>

      <!-- If done calculating matches and have results -->
      {#if matches.length > 0 && !isLoading}
        <div class="card mt-4">
          <header class="card-header">
            <p class="card-header-title">Match Results</p>
          </header>
          <div class="card-content">
            <div class="content">
              <table class="table is-fullwidth is-striped">
                <thead>
                  <tr>
                    <th>Potential Match</th>
                    <th>Match ID</th>
                    <th>Result</th>
                  </tr>
                </thead>
                <tbody>
                  {#each matches as match}
                  <tr>
                    <!-- If the selected horse is the stallion, show the mare's info, otherwise show the stallion's info -->
                    {#if match.stallion_id === Number(selectedHorseId)}
                      <td>{match.mare_id}</td>
                      <td>{match.mare_name}</td>
                    {:else}
                      <td>{match.stallion_id}</td>
                      <td>{match.stallion_name}</td>
                    {/if}
                
                    <td>
                      <span class={`tag ${match.color}`}>{match.prediction}</span>
                    </td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      {:else if !isLoading && selectedHorseId}
        <p class="has-text-centered">No matches found.</p>
      {/if}
    {/if}
  {/if}
</main>
