<script>
    export let horseAncestry;
  
    // A helper function to recursively collect rows of ancestry data
    function getAncestryRows(horse, depth = 0) {
      if (!horse) return [];
      const indent = '&nbsp;'.repeat(depth * 4); // indent by 4 spaces per generation
  
      // Format main horse row
      const rows = [{
        indent: indent,
        name: horse.name || 'Unknown',
        year: horse.year || 'Unknown',
        sex: horse.sex || 'Unknown',
        color: horse.color || 'Unknown',
        height: horse.height || 'Unknown',
        registry: horse.registry || 'Unknown',
        license: horse.license || 'Unknown',
        jump: horse.jump || 'Unknown'
      }];
  
      // If parents exist, recurse
      if (Array.isArray(horse.parents)) {
        for (const parent of horse.parents) {
          rows.push(...getAncestryRows(parent, depth + 1));
        }
      }
  
      return rows;
    }
  
    $: ancestryRows = horseAncestry ? getAncestryRows(horseAncestry) : [];
  </script>
  
  <style>
    table {
      width: 100%;
      border-collapse: collapse;
    }
    th, td {
      border-bottom: 1px solid #ccc;
      padding: 8px;
      text-align: left;
      vertical-align: top;
    }
    th {
      background-color: #f5f5f5;
      font-weight: bold;
    }
  </style>
  
  {#if horseAncestry}
    <table>
      <thead>
        <tr>
          <th>Horse</th>
          <th>Year</th>
          <th>Sex</th>
          <th>Color</th>
          <th>Height</th>
          <th>Registry</th>
          <th>License</th>
          <th>Jump</th>
        </tr>
      </thead>
      <tbody>
        {#each ancestryRows as row}
          <tr>
            <td>{@html row.indent}{row.name}</td>
            <td>{row.year}</td>
            <td>{row.sex}</td>
            <td>{row.color}</td>
            <td>{row.height}</td>
            <td>{row.registry}</td>
            <td>{row.license}</td>
            <td>{row.jump}</td>
          </tr>
        {/each}
      </tbody>
    </table>
  {:else}
    <p>No ancestry data available.</p>
  {/if}
  