<script>
  import { onMount } from "svelte";
  import { getToken, isAuthenticated } from './auth.js';

  let stallionID;
  let mareID;
  let selectedStallion = null;
  let selectedMare = null;
  let prediction = null;
  let isLoading = false;
  let progress = 0;
  let error = null;

  let allHorses = [];
  let stallions = [];
  let mares = [];
  // For filtering as user types in the textfields:
  let filteredStallions = [];
  let filteredMares = [];

  onMount(async () => {
    if ($isAuthenticated) {
      isLoading = true; // Start loading before fetching horses
      await fetchAllHorses();
      // Filter horses only after they've been fetched
      stallions = allHorses.filter(h => h['Horse Type'] === 'Stallion');
      mares = allHorses.filter(h => h['Horse Type'] === 'Mare');
      isLoading = false; // Done fetching horses
    }
  });

  async function fetchAllHorses() {
    try {
      const token = await getToken();
      if (!token) {
        console.log("Not authenticated, cannot fetch horses");
        return;
      }

      const response = await fetch(`http://127.0.0.1:5000/horses?page=1&per_page=9999`, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Failed to fetch horses:", errorText);
        return;
      }

      const data = await response.json();
      allHorses = data.data;
      console.log("Fetched all horses:", allHorses.length);
    } catch (err) {
      console.error("Error fetching horses:", err);
    }
  }

  function handleStallionInput() {
    // If empty, clear the filtered list and the selected stallion
    if (!stallionID) {
      filteredStallions = [];
      selectedStallion = null;
      return;
    }

    // Convert to string to compare against HorsetelexID (also as string)
    const input = stallionID.toLowerCase();

    // Filter stallions by partial match on HorsetelexID
    filteredStallions = stallions.filter(st => {
      return st.HorsetelexID.toString().toLowerCase().includes(input);
    });
  }

  // Called when user clicks a stallion from the filtered dropdown list
  function selectStallion(horse) {
    stallionID = horse.HorsetelexID.toString();
    selectedStallion = horse;
    filteredStallions = []; // Hide the dropdown after selection
  }

  // Same logic for Mare
  function handleMareInput() {
    if (!mareID) {
      filteredMares = [];
      selectedMare = null;
      return;
    }
    const input = mareID.toLowerCase();
    filteredMares = mares.filter(m => {
      return m.HorsetelexID.toString().toLowerCase().includes(input);
    });
  }

  function selectMare(horse) {
    mareID = horse.HorsetelexID.toString();
    selectedMare = horse;
    filteredMares = [];
  }

  const calculateOffspringPrediction = async () => {
    if (!stallionID || !mareID) {
      prediction = null;
      return;
    }

    try {
      isLoading = true; // Show loading while calculating prediction
      error = null;

      const token = await getToken();
      if (!token) {
        throw new Error('No authentication token available');
      }

      const sID = Number(stallionID);
      const mID = Number(mareID);

      const response = await fetch(`http://127.0.0.1:5000/match-pair`, {
        method: "POST",
        body: JSON.stringify({ stallion_id: sID, mare_id: mID }),
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });

      if (!response.ok) {
        console.error('Response status:', response.status);
        console.error('Response status text:', response.statusText);
        const errorText = await response.text();
        console.error('Error response:', errorText);
        throw new Error('Failed to calculate prediction');
      }

      prediction = await response.json();
      selectedStallion = prediction.stallion;
      selectedMare = prediction.mare;
      displayResults();
    } catch (e) {
      console.error("Error calculating prediction:", e);
      error = 'Failed to calculate prediction. Please try again.';
    } finally {
      isLoading = false; // Stop loading after calculation completes
    }
  };

  const getCompatibility = (prediction) => {
    const { common_ancestors, mare_summary, stallion_summary } = prediction;
    const commonAncestorsHighScore = common_ancestors.find(({ median_composite_score }) => median_composite_score > 0);
    const mareQuartile = mare_summary.jumping_score_adults_quartile;
    const stallionQuartile = stallion_summary.jumping_score_adults_quartile;

    if (mareQuartile >= 3 && stallionQuartile >= 3 && commonAncestorsHighScore) {
      prediction.explanation = "High quality existing offspring for stallion and mare, overlapping successful ancestry";
      return 4;
    }

    if (mareQuartile >= 3 && stallionQuartile >= 3) {
      prediction.explanation = "High quality existing offspring for stallion and mare, proven ancestry not detected";
      return 3;
    }

    if (mareQuartile === 2 && stallionQuartile >= 3) {
      prediction.explanation = "Higher quality offspring for stallion vs the mare, proven ancestry not detected";
      return 2;
    }

    if (stallionQuartile === 2 && mareQuartile >= 3) {
      prediction.explanation = "Higher quality offspring for mare vs the stallion, proven ancestry not detected";
      return 2;
    }

    if (mareQuartile === 2 && stallionQuartile === 2) {
      prediction.explanation = "Medium quality offspring for both mare and stallion, proven ancestry not detected";
      return 2;
    }

    return 1;
  };

  function displayResults() {
    const compatibility = prediction.predicted_jumping_class;
    console.log(compatibility);
    let result;
    let color;
    if (compatibility < 1.3) {
      result = `Poor ${Math.round(compatibility * 100) / 100}`;
      color = "is-danger";
    } else if (compatibility < 1.4) {
      result = `Fair ${Math.round(compatibility * 100) / 100}`;
      color = "is-warning";
    } else if (compatibility < 1.5) {
      result = `Good ${Math.round(compatibility * 100) / 100}`;
      color = "is-success";
    } else {
      result = `Excellent ${Math.round(compatibility * 100) / 100}`;
      color = "is-primary";
    }

    prediction = {
      ...prediction,
      result,
      color,
      compatibility,
    };
    console.log(prediction);
  }
</script>

<main>
  {#if !$isAuthenticated}
    <p class="has-text-centered">Please log in to match horses.</p>
  {:else if error}
    <div class="notification is-danger">
      {error}
    </div>
  {:else if isLoading}
    <!-- Loading logic similar to ViewHorses -->
    <div class="loading-container">
      <p class="has-text-centered">Loading data...</p>
      <progress class="progress is-info" max="100"></progress>
    </div>
  {:else if allHorses.length === 0}
    <div class="empty-state">
      <p class="has-text-centered">No horses found.</p>
    </div>
  {:else}
    <h1 class="title">Match Horses for Breeding</h1>

    <div class="columns">
      <div class="column">
        <div class="field">
          <label class="label">Select Stallion:</label>
          <div class="control has-icons-right">
            <input
              class="input"
              type="text"
              placeholder="Type Stallion ID"
              bind:value={stallionID}
              on:input={handleStallionInput}
            />
            <!-- If we have filtered results, show the dropdown -->
            {#if filteredStallions.length > 0}
              <div class="dropdown is-active">
                <div class="dropdown-menu">
                  <div class="dropdown-content">
                    {#each filteredStallions as st}
                      <a
                        href="#"
                        class="dropdown-item"
                        on:click={() => selectStallion(st)}
                      >
                        {st.Name} (ID: {st.HorsetelexID})
                      </a>
                    {/each}
                  </div>
                </div>
              </div>
            {/if}
          </div>
        </div>
      </div>

      <div class="column">
        <div class="field">
          <label class="label">Select Mare:</label>
          <div class="control has-icons-right">
            <input
              class="input"
              type="text"
              placeholder="Type Mare ID"
              bind:value={mareID}
              on:input={handleMareInput}
            />
            {#if filteredMares.length > 0}
              <div class="dropdown is-active">
                <div class="dropdown-menu">
                  <div class="dropdown-content">
                    {#each filteredMares as mr}
                      <a
                        href="#"
                        class="dropdown-item"
                        on:click={() => selectMare(mr)}
                      >
                        {mr.Name} (ID: {mr.HorsetelexID})
                      </a>
                    {/each}
                  </div>
                </div>
              </div>
            {/if}
          </div>
        </div>
      </div>
    </div>

    <div class="field">
      <div class="control">
        <button
          class="button is-primary is-fullwidth"
          on:click={calculateOffspringPrediction}
          disabled={isLoading}
        >
          {isLoading ? "Calculating..." : "Get Prediction"}
        </button>
      </div>
    </div>

    {#if isLoading}
      <div class="mt-4">
        <progress class="progress is-primary" value={progress} max="100">
          {progress}%
        </progress>
      </div>
    {/if}

    {#if prediction && !isLoading}
      <div class="card mt-4">
        <header class="card-header">
          <p class="card-header-title">Breeding Prediction</p>
        </header>
        <div class="card-content">
          <div class="content">
            <div class="columns is-mobile is-centered">
              <div class="column is-narrow">
                <span class={`tag is-large ${prediction.color}`}>
                  {prediction.result}
                </span>
              </div>
            </div>
            <table class="table is-fullwidth mt-4">
              <tbody>
                <tr>
                  <!-- Use colspan="2" to span the full width of the parent table -->
                  <td colspan="2">
                    <strong>Regression Input Data</strong>
                    <table class="table is-fullwidth is-bordered is-size-7 mt-2">
                      <tbody>
                        {#each Object.entries(prediction.reg_input) as [key, value]}
                          <tr>
                            <td><strong>{key}</strong></td>
                            <td>{value}</td>
                          </tr>
                        {/each}
                      </tbody>
                    </table>
                  </td>
                </tr>
                <tr>
                  <td><strong>Stallion:</strong></td>
                  <td>{selectedStallion.Name}</td>
                </tr>
                <tr>
                  <td><strong>Mare:</strong></td>
                  <td>{selectedMare.Name}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    {/if}
  {/if}
</main>
